@import '/src/styles/variables.scss';
@import '/src/styles/mixins.scss';

.contact {
  @extend %container;
}

.contact__title {
  @extend %title;
}

.contact__text_container {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 16px;
  margin-bottom: 16px;

  @media (768px <= width <= 1279px) {
    gap: 24px;
    margin-bottom: 24px;
  }
}

.contact__icon {
  width: 36px;
  height: 36px;
}

.contact__text {
  @include text(400, 16px, 19px);
  text-align: center;
  text-transform: uppercase;

  @media (768px <= width <= 1279px) {
    width: 397px;
  }
}
